<template>
  <div>
    <h4 v-if="log" @click="signout">logout</h4>
    <h1>Welcome to asr racing laboratory</h1>    
    <div v-if="log">
      <h3>Hi {{user}}</h3>    
    </div>
    <div v-if="statTok">
      <h2 @click="changestat">{{statuser}}</h2>
      <div class="card-deck">
        <Register v-if="statreg" class="slide-in-elliptic-top-fwd"></Register> 
        <Login v-if="statlog" class="slide-in-elliptic-top-fwd"></Login>
      </div> 
      <router-view/>
    </div>   
    <Content></Content>
  </div>
</template>

<script>
    import Register from './Register'
    import Login from './Login'
    import Content from './Content'

    export default {
        name: 'Home',
        components: {
            'Register': Register,
            'Login' : Login,
            'Content' : Content
        },
        data(){
          return{
            statuser: "Click me if you want to Register",
            statreg: false,
            statlog: true,
            token: window.localStorage["commerce-token"],
            role:window.localStorage["com-role"],
            statTok: true,
            log: false,
            user:window.localStorage["com-user"]
          }
        },
        created () {
            if (this.token) {
              this.statTok=false
              this.log=true
            }else{
              this.statTok=true
              this.log=false
            }          
        },
        methods:{
          changestat(){
            if(this.statuser == "Click me if you want to Register"){
              this.statuser = "Click me if you want to Login"
              this.statreg = true
              this.statlog = false
            }else{
              this.statuser = "Click me if you want to Register"
              this.statreg = false
              this.statlog = true
            }
          },
          signout(){
            console.log('siniiii')
            localStorage.removeItem('commerce-token')
            localStorage.removeItem('com-user')
            localStorage.removeItem('com-role')
            window.location.reload(true);
          }
        }
    }
</script>

