<template>
    <div class="inputtask">    
        <div class="wrap" v-if="role == 'admin'">
            <h1>Add Item</h1>
            <button class="btn btn-outline-secondary" data-toggle="modal" data-target="#create">Add New Item</button>
            <div class="modal" tabindex="-1" role="dialog" id='create'>
                <div class="modal-dialog" role="document">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">Input Item</h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                            <form >
                                <div class="form-group">    
                                    <p>Item name :</p>
                                    <input type="text" class="form-control" placeholder="Enter your item name" v-model="itemName">
                                    <p>Item image :</p>
                                    <input type="file" class="form-control" placeholder="Enter your image file" id="imageFile">    
                                    <p>Item price :</p>
                                    <input type="number" class="form-control" placeholder="Enter your item price" v-model="itemPrice">
                                    <p>Item category :</p>
                                    <input type="text" class="form-control" placeholder="Enter your item category" v-model="itemCategory">
                                    <p>Item quantity :</p>
                                    <input type="number" class="form-control" placeholder="Enter your item quantity" v-model="itemQuantity">
                                </div>
                            </form>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-primary" @click="uploadItem" data-dismiss="modal">submit item</button>
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>        
        <br/>
        <div class="d-flex flex-row">
            <div class="p-3">
                <form class="form-inline my-2 my-lg-0" v-on:submit.prevent="search">
                    <input class="form-control mr-sm-2" type="search" placeholder="Search by name" aria-label="Search" v-model="query">
                    <button class="btn btn-outline-success my-2 my-sm-0" type="submit" @click="search">Search</button>                    
                </form>
            </div>
            <div class="p-3">
                <button class="btn btn-outline-success my-2 my-sm-0" type="submit" @click="getBike">Bike Category</button>
            </div>
            <div class="p-3">
                <button class="btn btn-outline-success my-2 my-sm-0" type="submit" @click="getExhaust">Exhaust Category</button>
            </div>
            <div class="p-3">
                <button class="btn btn-outline-success my-2 my-sm-0" type="submit" @click="getAcc">Accessories Category</button>
            </div>
            <div class="p-3">
                <button class="btn btn-outline-success my-2 my-sm-0" data-toggle="modal" data-target="#cart">Cart</button>
                <div class="modal" tabindex="-1" role="dialog" id='cart'>
                    <div class="modal-dialog" role="document">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h5 class="modal-title">Input Item</h5>
                                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div class="modal-body">
                                <div class="p-2" v-for="(item, index) in carts" v-bind:key="index">
                                <div class="card-deck" style="padding-top:25px;">
                                    <div class="card">
                                        <div class="container">
                                            <div class="row">
                                                <div class="col">
                                                    <img class="img-fluid" :src="item.item.imageUrl" alt="Card image cap" style=" width:auto; margin:auto; padding: 5px 5px 5px 5px;">
                                                </div>
                                                <div class="col">
                                                    <div class="card-footer">
                                                        <h5 class="card-title">{{item.item.name}}</h5>
                                                        <p class="card-text">Rp. {{new Intl.NumberFormat('en-US').format(item.item.price)}}</p>
                                                        <p class="card-text">Quantity : {{item.qty}} unit</p>
                                                        <small class="text-muted"><button type="button" class="btn btn-dark" @click="pluss()">Add qty</button></small>
                                                        <small class="text-muted"><button type="button" class="btn btn-dark" @click="minus()">Min qty</button></small>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>                   
                                </div>
                            </div>    
                            </div>
                            <div class="modal-footer">
                                <button type="button" class="btn btn-primary" @click="uploadItem" data-dismiss="modal">check out</button>
                                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <br>
        <Carousel></Carousel>        
        <br/>
        <div class="d-flex flex-column">
            <div class="p-2" v-for="(item, index) in items" v-bind:key="index">
                <div class="card-deck" style="padding-top:25px;">
                    <div class="card">
                        <div class="container">
                            <div class="row">
                                <div class="col">
                                    <img class="img-fluid" :src="item.imageUrl" alt="Card image cap" style=" width:auto; margin:auto; padding: 5px 5px 5px 5px;">
                                </div>
                                <div class="col">
                                    <div class="card-footer">
                                        <h5 class="card-title">{{item.name}}</h5>
                                        <p class="card-text">Rp. {{new Intl.NumberFormat('en-US').format(item.price)}}</p>
                                        <p class="card-text">Quantity : {{item.quantity}} unit</p>
                                        <p class="card-text">Category : {{item.category}}</p>
                                        <small class="text-muted"><button type="button" class="btn btn-dark" @click="addToCart(item)">Add to cart</button></small>
                                        <small class="text-muted"  v-if="role == 'admin'"><button type="button" class="btn btn-dark" :data-id="item._id" @click="deleteItem">Delete</button></small>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>                   
                </div>
            </div>    
        </div>    
        <h3>{{ nothing }}</h3>
    </div>
    
</template>

<script>
import Carousel from './Carousel'

export default {
 name: "Content",
 components: {
            'Carousel': Carousel,
        },
 data: function(){
     return {
        itemName:'',
        itemCategory:'',
        itemPrice:'',
        itemQuantity:'',
        items:'',
        stat:'',
        query:'',
        nothing:'',
        role:window.localStorage["com-role"],
        carts:[],
        cartItem:{}
     }
 },
 created () {
     this.getall()
 },
 methods:{     
     deleteItem(){
        let id = event.srcElement.dataset.id
        let headers = {
                token : window.localStorage["commerce-token"]
            }
        axios.delete(`https://arcane-falls-85350.herokuapp.com/upload/${id}`, {headers:headers})
        .then( response => {
            swal("successfully deleted");
            this.getall()
        })
        .catch( err => {
            this.getall()
            this.error = err.response.data.message
        })
     },
     addToCart(item){
         let cart = {
             item,
             qty: 1             
         }
         this.carts.push(cart)
         swal("added to cart");
     },
     search(){
         axios.get(`https://arcane-falls-85350.herokuapp.com/upload/search?name=${this.query}`)
        .then( response => {            
            this.items = response.data.data
            if(response.data.message == 'nothing to show'){
                this.nothing = `${response.data.message} for keyword : '${this.query}'` 
            }else{
                this.nothing =''
            }            
        })
        .catch( err => {
        })
     },
    searchCat(category){
         axios.get(`https://arcane-falls-85350.herokuapp.com/upload/searchCat?category=${category}`)
        .then( response => {            
            this.items = response.data.data
            console.log(this.items)
            if(response.data.message == 'nothing to show'){
                this.nothing = `${response.data.message} for keyword : '${category}'` 
            }else{
                this.nothing =''
            }            
        })
        .catch( err => {
        })
     },
     getall(){
        let self = this
        axios.get('https://arcane-falls-85350.herokuapp.com/upload')
        .then( response => {            
            console.log(response.data.data)
            self.items = response.data.data

        })
        .catch( err => {
        })
     },
    getBike(){
        this.searchCat('bike')
    },
    getExhaust(){
        this.searchCat('exhaust')
    },
    getAcc(){
        this.searchCat('accessories')
    },
    uploadItem(){
       let fileInput = document.querySelector('#imageFile');
        console.log(fileInput)
        let headers = {
                token : window.localStorage["commerce-token"]
            }
        let formData = new FormData()
        formData.append('itemName', this.itemName)
        formData.append('image', fileInput.files[0])
        formData.append('itemCategory', this.itemCategory)
        formData.append('itemPrice', this.itemPrice)
        formData.append('itemQuantity', this.itemQuantity)
        console.log(formData)
        axios.post('https://arcane-falls-85350.herokuapp.com/upload',formData,{headers:headers})
        .then( response => {
            console.log(response)
            swal("successfully uploaded");
            this.getall()
        })
        .catch( error => {
            console.log('error dari upload.html',error)
        })
    }   
    
 }
}
</script>

<style>
.inputtask{
    margin-left:150px;
    margin-right:150px;
}
</style>


